<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <div class="title">
          <b>{{ tool.title }}</b>
          <div>
            <b>作者：</b>{{ tool.teacher }}
            <b>时间：</b>{{ tool.time }}
            <b>售价：</b>{{ tool.price }}
          </div>
        </div>
      </div>
      <div>
        <share :config="config" data-disabled="google,twitter,facebook,linkedin,douban,tencent,diandian" data-qq-title="好的源码，大家分享"></share>
        <div class="introduce">
          <b>介绍 </b>{{ tool.remark }}
        </div>
        <div v-for="(o) in tool.json" :key="o">
          <div class="img" v-if="o.type === 'img'">
            <img :src="o.content"/>
          </div>
          <div class="img_small" v-if="o.type === 'img_small'">
            <el-row :gutter="24">
              <div v-for="(i) in o.content" :key="i">
                <el-col :span="8">
                  <img :src="i"/>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="img_big" v-if="o.type === 'img_big'">
            <el-row :gutter="24">
              <div v-for="(i) in o.content" :key="i">
                <el-col :span="24">
                  <img :src="i"/>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="text" v-if="o.type === 'text'">
            {{o.content}}
          </div>
          <div class="tool" v-if="o.type === 'tool'">
            百度网盘地址：<a :href="o.url" target="_Blank"><span>{{o.url}}</span></a>
            <br/>
            密码：<span>{{o.password}}</span>
            <br/>
            <img :src="tool.img"/>
          </div>
        </div>
      </div>
      <div class="introduce">
          <b>声明：</b> 版权所有，盗版必究。
      </div>
    </el-card>
  </div>
</template>

<script>
import { tool } from '../../../static/js/tool.js'

export default {
  data () {
    return {
      id: this.$route.query.id,
      tool: tool.data[this.$route.query.id - 1]
    }
  }
}
</script>

<style scoped>
.img img {
  width: 100%;
  height: 100%;
  border: "1";
  border-width: 1px;
  border-style: solid;
  margin-top: 10px;
}
.img_small img {
  width: 100%;
  height: 700px;
  border: "1";
  border-width: 1px;
  border-style: solid;
  margin-top: 10px;
}
.img_big img {
  width: 100%;
  height: 500px;
  border: "1";
  border-width: 1px;
  border-style: solid;
  margin-top: 10px;
}
.text {
  text-indent:25px;
}
.title {
  font-size: 20px;
}
.title div {
  font-size: 15px;
  color: red;
  margin-top: 10px;
}
.introduce {
  padding: 10px;
  background-color: #EFF2F7;
  margin-bottom: 10px;
  border: solid 1px #F3F3F3;
  line-height: 23px;
}
</style>



